@charset 'utf-8';

@font-face{
  font-family: 'Regular';
  src: url('../fonts/Regular.otf') format('otf')
}
@font-face{
  font-family: 'Semibold';
  src: url('../fonts/SemiBold.otf') format('otf')
}
@font-face{
  font-family: 'Medium';
  src: url('../fonts/Medium.otf') format('otf')
}
@font-face{
  font-family: 'Bold';
  src: url('../fonts/Bold.otf') format('otf')
}
*{box-sizing: border-box;}
ul,li{
  list-style: none;
}
body{
  font-family: 'Regular';
  color: #333;
}
.Semibold{
  font-family: 'Semibold';
}
.Medium{
  font-family: 'Medium';
}
.Bold{
  font-family: 'Bold';
}
.white{
  color: #fff;
}
.flex{
  display: flex;
}
.pointer{
  cursor: pointer;
}
.top{
  display: none;
}
.bannerC>.navbar{
  z-index: 100000000;
  left: 0;
  right: 0;
  top: 30px;
  /* top: 0; */
  background: transparent;
  padding: 0;
}
.bannerC .navbar-brand{
  display: block;
  width: 19%;
}
.bannerC .navbar-brand .logoIcon{
  display: block;
  width: 100%;
  min-width: 120px;
}
.bannerC>.navbar .p0{
  padding: 0;
}
.bannerC .navbar-collapse{
  padding-right: 4%;
  max-height: 100%;
}
.nav-link{
  white-space: nowrap;
  font-weight: 900;
}
.bgWhite{
  background-color: #fff;
}
.bgGray{
  background-color: #FAFAFA;
}
.bgDeeepGray{
  background-color: #F6F6F6;
}
.bgOrange{
  background-color: #C19451;
}
.bgDeepGWhite{
  background-color: #FEFCFD;
}
.containers{
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.content{
  width: 80%;
}

.title-center{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.title-start{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}
.title-s{
  display: inline-flex;
  flex-direction: column;
  align-items: center;
}
.title-b{
  font-weight: bold;
  color: #333333;
}
.title-sub{ font-weight: 400;
  color: #a2a2a2;
}
.ellipsis{
  overflow:hidden;
  white-space:nowrap;
  text-overflow:ellipsis;
  width:100%;
}
.ellipsis-double{
  overflow:hidden;
  text-overflow:ellipsis;
  width:100%;
  display:-webkit-box;
  -webkit-box-orient:vertical;
  -webkit-line-clamp:2;
}
a{
  text-decoration: none;
}
.flex-row1{
  display: flex;
}
.flex1{
  flex-shrink: 1;
}
.mb10{
  margin-bottom: 10px;
}
.footer{
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  line-height: 25px;
  text-align: center;
}
.colorWhite{
  color: #fff;
}
.colorGray{
  color: #999999;
}
.bannerImg{
  display: block;
  width: 100%;
}
.textIndex2 p{
  text-indent: 2em;
}

@media (min-width:1200px) {
  .imgC{
    padding-top: 120px!important;
  }
  .bannerC>.navbar{
    background-color: #C19451;
    height: 120px;
  }
  .nav-link{
    margin: 0 10px;
  }
  .pt25{
    padding-top: 25px;
  }
  .pb25{
    padding-bottom: 25px;
  }
  .font18{
    font-size: 18px;
  }
  .font20{
    font-size: 20px;
  }
  body{
    font-size: 22px;
  }
  .font40{
    font-size: 40px;
  }
  .font80{
    font-size: 80px;
  }
  .font100{
    font-size: 100px;
  }



  .font38{
    font-size: 38px;
  }
  .title-line{
    width: 34px;
    height: 6px;
  }
  .font16{
    font-size: 16px;
  }
  .font14{
    font-size: 14px;
  }
  .mb30{
    margin-bottom: 30px;
  }
  .mt30{
    margin-top: 30px;
  }
  .content{
    padding: 4.8% 0;
  }


  .font36{
    font-size: 36px;
  }

  .font26{
    font-size: 26px;
  }

  .font32{
    font-size: 32px;
  }

  .font50{
    font-size: 50px;
  }
  .font70{
    font-size: 70px;
  }
  .mb50{
    margin-bottom: 50px;
  }

}
@media (min-width: 768px) and (max-width:1200px) {
  .imgC{
    padding-top: 100px!important;
  }
  .bannerC>.navbar{
    background-color: #C19451;
    height: 100px;
  }
  .pt25{
    padding-top: 12px;
  }
  .pb25{
    padding-bottom: 12px;
  }
  .font20{
    font-size: 15px;
  }
  body{
    font-size: 17px;
  }
  .font40{
    font-size: 35px;
  }
  .font80{
    font-size: 50px;
  }
  .font70{
    font-size: 50px;
  }

  .font38{
    font-size: 34px;
  }
  .title-line{
    width: 34px;
    height: 6px;
  }
  .font16{
    font-size: 14px;
  }
  .font14{
    font-size: 12px;
  }
  .mb30{
    margin-bottom: 20px;
  }
  .mt30{
    margin-top: 20px;
  }
  .content{
    padding: 10% 0;
  }

  .font36{
    font-size: 30px;
  }

  .font26{
    font-size: 20px;
  }
  .font32{
    font-size: 26px;
  }

  .font50{
    font-size: 35px;
  }


  .mb50{
    margin-bottom: 30px;
  }
}
@media (max-width:768px) {
  .imgC{
    padding-top: 70px!important;
  }
  .logoIcon{
    height: 40px;
  }
  .top{
    position: absolute;
    width: 100%;
    height: 41px;
    top: 0;
    left: 0;
    background-color: #C19451;
    z-index: -1;
    display: block;
  }
  .bannerC>.navbar{
    background-color: transparent;
  }
  .container-fluid{
    background-color: transparent;
  }
  .pt25{
    padding-top: 6px;
  }
  .pb25{
    padding-bottom: 6px;
  }
  .font20{
    font-size: 12px;
  }
  body{
    font-size: 14px;
  }
  .font40{
    font-size: 20px;
  }
  .font80{
    font-size: 30px;
  }
  .font70{
    font-size: 30px;
  }
  .font100{
    font-size: 100px;
  }
  .bannerC .navbar-collapse{
    background: rgba(0, 0, 0, .3);
  }
  .swiper-button-next,.swiper-button-prev,.mouseUpDown{
    display: none;
  }
 

  .font38{
    font-size: 28px;
  }
  .title-line{
    width: 34px;
    height: 4px;
  }
  .font16{
    font-size: 12px;
  }
  .font14{
    font-size: 12px;
  }
  .mb30{
    margin-bottom: 20px;
  }
  .mt30{
    margin-top: 40px;
  }
  .content{
    padding: 15% 0;
  }

  .font36{
    font-size: 26px;
  }

  .font26{
    font-size: 16px;
  }

  .font32{
    font-size: 18px;
  }

  .font50{
    font-size: 30px;
  }


  .mb50{
    margin-bottom: 20px;
  }
  .footer{
    flex-direction: column;
  }
}
@media (min-width:1400px){
  .nav-link{
    margin: 0 20px;
  }
}
.navbar-toggler{
  background-color: transparent;
  /* border-color: #fff; */
  color: #fff;
}

