.changeLanguage{
  display: flex;
  width: 100%;
  background-color: #fff;
  height: 30px;
  font-size: 16px;
  align-items: center;
  color: #C19451;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 100;
  padding-left: 2%;
}
.changeLanguage>a{
  margin: 0 20px;
  color: #C19451;
}